
.nav ul{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    background-color: #000;
    color: #fff;

    list-style: none;
}

.nav li{
    padding: 1em;
}

.nav a, .nav a:visited{
    color: #fff;
    text-decoration: none;
}

.nav a:hover, .nav a:active{
    color: aqua;
    text-decoration: none;
    
}
